.sidebar {
  margin: 86px 0 0;

  border-right: 1.5px solid #17171780;
}

.sidebar-list {
  list-style: none;

  width: 214px;

  padding: 0 50px 54px 0;
  margin: 0;
  box-sizing: border-box;
}

.sidebar-link {
  font-weight: 600;
  text-transform: uppercase;
  color: #16161680;
  text-decoration: none;
}

.sidebar-link:focus {
  outline: 0;
}

.sidebar-link:focus-visible {
  outline: 1.5px solid #161616;
}

.sidebar-item {
  margin-bottom: 11px;
}

.sidebar-item:last-child {
  margin-bottom: 0;
}

.sidebar-item_active {
  color: #161616;
}
